@media (max-width: 1024px) {
  .h2o-header .h2o-burger {
    --burger-width: 24px;
    --burger-height: 16px;
    --burger-line-height: 2px;
    --border-radius: 3px;
    position: relative;
    border: none;
    padding: 0;
    width: var(--burger-width);
    height: var(--burger-height);
    color: rgba(var(--cl-a), 1);
    background-color: transparent;
    flex-shrink: 0;
    cursor: pointer;
    transition: all 0.3s;
  }
  .h2o-header .h2o-burger::before, .h2o-header .h2o-burger::after {
    content: "";
    position: absolute;
    right: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    border-radius: var(--border-radius);
  }
  .h2o-header .h2o-burger::before {
    top: 0;
  }
  .h2o-header .h2o-burger::after {
    width: 65%;
    top: calc(100% - var(--burger-line-height));
  }
  .h2o-header .h2o-burger__line {
    position: absolute;
    right: 0;
    top: 50%;
    width: 85%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
    border-radius: var(--border-radius);
  }
  .h2o-header .h2o-burger.active::before {
    top: 45%;
    transform: rotate(45deg);
